<template>
  <div class="container">
    <el-row>
      <el-col :span="8">
        <h1 class="title">{{pageName}}</h1>
        <label for="">父分类</label>
        <el-cascader
                        size="medium"
                        style="width: 100%;"
                        :options="categoryList"
                        @change="handleChange"
                        ></el-cascader>

        <label for="">分类名称</label><el-input v-model="data.name" placeholder="请输入名称"></el-input>

      </el-col>
    </el-row>
    <div class="footerButton border-shadow">
      <el-button type="primary" @click="submitTask()" style="vertical-align: top">提交</el-button>
    </div>
  </div>
</template>

<script>
    export default {
        name: "addTask",
        data() {
            return {
                pageName: '新增',
                task: {
                    supplier_platform: '',
                    supplier_name: '',
                    memo: ''
                },
                data:{
                    cid:'',
                    name:'',
                },
                categoryList: [
                {
                    value:'1',
                    label:'111',
                    children:[
                    {
                        value:'2',
                        label:'222',
                        children:[
                        {
                            value:'3',
                            label:'333',
                            children:[
                            {
                                value:'4',
                                label:'444',
                            }
                            ]
                        }
                        ]
                    },
                    ]
                }
                ],
            }
        },
        methods: {
            handleChange(value) {
                let length = value.length;
                this.data.cid = value[length-1];
            },
            submitTask() {
                let url = "/v1/categories";
                let params = {};
                params.name = this.data.name;
                params.parent_id = this.data.cid;
                params.sort = '80';

                let that = this;
                this.$axios.post(url,params).then((res) => {
                    console.log(res);

                    that.$message({
                        type: 'success',
                        message: '新增成功!'
                    });

                }).catch((err) => {

                    that.$message({
                        type: 'error',
                        message: '新增失败!'
                    });
                })
            },
            initTable() {
                let url = '/v1/category/initdatas';
                let that = this;
                this.$axios.get(url).then((res) => {

                    let cate_list = res.data.data.cate_list;
                    console.log(cate_list);
                    var list = [];
                cate_list.forEach(function(row, index) {
                    if (row.parent_id == 0) {
                        let temp = {
                            value:row.id,
                            label:row.name,
                            children:[]
                        };
                        cate_list.forEach(function(item, key){
                            if (item.parent_id == row.id) {
                                temp.children[key] = {};
                                temp.children[key].value = item.id;
                                temp.children[key].label = item.name;
                                cate_list.forEach(function(node, i){
                                    if(node.parent_id == item.id) {
                                        temp.children[key].children = [];
                                        temp.children[key].children[i] = {};
                                        temp.children[key].children[i].value = node.id;
                                        temp.children[key].children[i].label = node.name;
                                        cate_list.forEach(function(k, v){
                                            if (k.parent_id == node.id) {
                                            temp.children[key].children[i].children = [];
                                            temp.children[key].children[i].children[v] = {};
                                            temp.children[key].children[i].children[v].value = k.id;
                                            temp.children[key].children[i].children[v].label = k.name;
                                            }
                                        })
                                    }
                                });
                            }
                        });
                        list.push(temp);
                    }
                });
                that.categoryList = list;
                console.log(res)
                }).catch((err) => {
                    console.log(err);
                });
            },
        },
        created() {
            this.initTable();
        }
    }
</script>

<style scoped lang="stylus">
  .container
    .el-row
      margin 30px 30px 0px 30px
    label
      display block
      font-weight bold
      font-size: 20px
      margin: 10px 0
    .title
      margin-bottom: 50px
    .button
      margin-top: 20px
    .upload-img
      display inline-block
      margin-right 30px
    .footerButton
      position absolute
      left: 0
      bottom 0
      width: 100%
      padding 13px 30px
      box-sizing border-box
      background-color: hsla(0,0%,100%,.9);
      z-index 2
      button
        float: right;

</style>



